<template>
  <f7-page>
    <!-- Header -->
    <f7-navbar>
      <f7-nav-title class="logo">
        <img src="../assets/logo.png" alt="Me2We">
      </f7-nav-title>
    </f7-navbar>
    <div class="select-title">選擇場次</div>
    <f7-page class="select">
      <ul>
        <li
          v-for="(item, index) in eventList"
          :key="index"
          class="select-item"
          :style="{ backgroundImage: 'url(' + item.url + ')' }"
          @click="$f7router.navigate('/home/')"
        >
          <!-- 區塊 -->
          <div class="select-item-block">
            <!-- 內容 -->
            <div class="content">
              <h1 v-text="item.name"></h1>
              <p v-text="formatDate(item.date, '-')"></p>
            </div>
            <!-- 按鈕 -->
            <div class="buttons">
              <a href="#" class="link">
                <i class="f7-icons">arrow_right_fill</i>
              </a>
            </div>
          </div>
        </li>
      </ul>
    </f7-page>
  </f7-page>
</template>

<script>
export default {
  name: "Select",
  data() {
    return {
      // 場次列表
      eventList: [
        {
          id: 1,
          url: require("@/assets/select/event1.jpg"),
          name: "ART展演活動",
          date: "2019/6/13"
        },
        {
          id: 2,
          url: require("@/assets/select/event2.jpg"),
          name: "潮流品牌介紹",
          date: "2019/6/12"
        },
        {
          id: 3,
          url: require("@/assets/select/event3.jpg"),
          name: "浪漫婚禮",
          date: "2019/6/11"
        }
      ]
    };
  },
  methods: {
    // 日期格式設定
    formatDate: function(date, separator) {
      var dateList = new Array();
      var theDate = new Date(date);
      var yyyy = theDate.getFullYear();
      dateList.push(yyyy);
      var mm =
        theDate.getMonth() < 9
          ? "0" + (theDate.getMonth() + 1)
          : theDate.getMonth() + 1;
      dateList.push(mm);
      var dd =
        theDate.getDate() < 10 ? "0" + theDate.getDate() : theDate.getDate();
      dateList.push(dd);

      return dateList.join(separator);
    }
  },
  created() {},
  mounted() {}
};
</script>

<style scoped lang="scss" src="../scss/select.scss"></style>
